import React, { useState, useContext } from 'react';
import { AiOutlineRight, AiOutlineLeft } from 'react-icons/ai';
import DatePicker from 'sassy-datepicker';
import moment from 'moment';
import { subDays, addDays } from 'date-fns';
import './DateView.scss';
import { TodoContext } from '../context/TodoStore';
// developer : rojin
function DateView() {
	let { updateCurrentTodos, currentDate, setCurrentDate } =
		useContext(TodoContext);
	// const [date, setDate] = useState(currentDate);
	const [visible, setVisible] = useState(false);

	// const setTodos = props.setTodos;

	const modifyDate = (nextDate) => {
		// setDate(nextDate);
		currentDate = nextDate;
		setCurrentDate(nextDate);
		updateCurrentTodos(nextDate);
	};

	const increaseDate = () => {
		const nextDate = addDays(currentDate, 1);
		modifyDate(nextDate);
	};

	const decreaseDate = () => {
		const nextDate = subDays(currentDate, 1);
		modifyDate(nextDate);
	};

	return (
		<>
			<div className="DateView">
				<button className="LeftArrow" onClick={decreaseDate}>
					<AiOutlineLeft />
				</button>
				<button className="Date" onClick={() => setVisible(!visible)}>
					{moment(currentDate).format('YYYY/MM/DD')}
				</button>
				<button className="RightArrow" onClick={increaseDate}>
					<AiOutlineRight />
				</button>
			</div>
			<div>
				{visible ? (
					<DatePicker
						className="popUp"
						selected={currentDate}
						onChange={(date) => {
							setVisible(!visible);
							modifyDate(date);
						}}
					/>
				) : null}
			</div>
		</>
	);
}

export default DateView;
