import React, { useContext } from 'react';
import TodoItem from './TodoItem';
import './TodoListView.scss';
import { TodoContext } from '../context/TodoStore';
function TodoListView() {
	const { currentTodos, setTodos, onRemove, onToggle } =
		useContext(TodoContext);

	const onItemRemove = (id) => {
		setTodos(onRemove(id));
		console.log('onItemRemove ' + id);
	};
	const onItemToggle = (id) => {
		setTodos(onToggle(id));
		console.log('onItemRemove ' + id);
	};

	return (
		<div className="TodoListView">
			{currentTodos.map((todo) => (
				<TodoItem
					todo={todo}
					onItemRemove={onItemRemove}
					onItemToggle={onItemToggle}
				/>
			))}
		</div>
	);
}

export default TodoListView;
