import React, { createContext, useState, useEffect } from 'react';
import { getTodoList } from '../components/FirebaseApp';
export const TodoContext = createContext();

let init = false;

function isSameDay(src, dst) {
	return (
		src.getFullYear() === dst.getFullYear() &&
		src.getMonth() === dst.getMonth() &&
		src.getDate() === dst.getDate()
	);
}

export function TodoStore(props) {
	async function insertFirebaseItems() {
		const fireItems = await getTodoList();
		for (const item of fireItems) {
			console.log(item);
			onInsert(item.title, item.content, item.checked);
		}
	}

	useEffect(() => {
		if (!init) {
			insertFirebaseItems();
			updateCurrentTodos(currentDate);
			init = true;
		}
	});

	let [currentDate, setCurrentDate] = useState(new Date());
	let [todos, setTodos] = useState([
		{
			id: 0,
			title: '  ',
			content: '  ',
			checked: true,
			date: new Date(1990, 0, 0),
		},
		{
			id: 1,
			title: '  ',
			content: ' ',
			checked: true,
			date: new Date(),
		},
		{
			id: 2,
			title: '    ',
			content: '    ',
			checked: false,
			date: new Date(),
		},
	]);
	let [currentTodos, setCurrentTodos] = useState([]);
	let [nextId, setNextId] = useState(3);

	const updateCurrentTodos = (nextDate) => {
		var nextCurrentTodos = todos.filter((todo) =>
			isSameDay(todo.date, nextDate),
		);
		setCurrentTodos(nextCurrentTodos);
		return nextCurrentTodos;
	};

	const getCurrentTodos = () => {
		console.log(currentTodos);
		return currentTodos;
	};

	const getTodos = () => {
		console.log(todos);
		return todos;
	};

	const getTodo = (id) => {
		return todos.find((todo) => todo.id === id);
	};

	const onInsert = (title, content, checked = false) => {
		const todo = {
			id: nextId,
			title: title,
			content: content,
			checked: checked,
			date: currentDate,
		};
		todos = todos.concat(todo);
		setTodos(todos);
		nextId = nextId + 1;
		updateCurrentTodos(currentDate);
		return getTodos();
	};

	const onModify = (id, title, content) => {
		todos = todos.map((todo) =>
			todo.id === id ? { ...todo, title: title, content: content } : todo,
		);
		setTodos(todos);
		updateCurrentTodos(currentDate);
		return getTodos();
	};

	const onToggle = (id) => {
		todos = todos.map((todo) =>
			todo.id === id ? { ...todo, checked: !todo.checked } : todo,
		);
		setTodos(todos);
		updateCurrentTodos(currentDate);
		return getTodos();
	};

	const onRemove = (id) => {
		todos = todos.filter((todo) => todo.id !== id);
		setTodos(todos);
		updateCurrentTodos(currentDate);
		return getTodos();
	};
	return (
		<TodoContext.Provider
			value={{
				currentDate,
				setCurrentDate,
				todos,
				setTodos,
				currentTodos,
				getCurrentTodos,
				updateCurrentTodos,
				// currentDate get/set
				// setCurrentDate,
				// getCurrentDate,
				// todos get
				getTodos,
				getTodo,
				// todos CRUD
				onInsert,
				onModify,
				onToggle,
				onRemove,
			}}
		>
			{props.children}
		</TodoContext.Provider>
	);
}

export default TodoStore;
