import React, { useState, useContext } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { TodoContext } from '../context/TodoStore';

// developer : hakkyu

function DetailView() {
	const { getTodo, onInsert, onModify } = useContext(TodoContext);
	const navigate = useNavigate();
	const params = useParams();
	const id = params.id !== undefined ? parseInt(params.id, 10) : null;

	let isEditMode = false;
	let title = '';
	let content = '';

	if (id === null) {
		isEditMode = true;
	} else {
		const todo = getTodo(id);
		title = todo.title;
		content = todo.content;
		console.log(todo);
	}

	const [editMode, setEditMode] = useState(isEditMode);
	const [titleText, setTitleText] = useState(title);
	const [contentText, setContentText] = useState(content);

	if (editMode) {
		return (
			<form
				className="detail-view"
				onSubmit={(event) => {
					if (id === null) {
						onInsert(titleText, contentText);
						navigate('/');
					} else {
						onModify(id, titleText, contentText);
						setEditMode(false);
					}
					//   event    default  .
					event.preventDefault();
				}}
			>
				<textarea
					className="title"
					value={titleText}
					onChange={(event) => setTitleText(event.target.value)}
				/>
				<textarea
					className="content"
					value={contentText}
					onChange={(event) => setContentText(event.target.value)}
				/>
				{/* input  onClick attribute   . */}
				<input type="submit" value="" />
				<input
					type="button"
					value=""
					onClick={() => {
						setEditMode(false);
						setTitleText(title);
						setContentText(content);
						console.log('');
						console.log(title);
						console.log(content);
					}}
				/>
			</form>
		);
	} else {
		return (
			<div className="detail-view">
				<div className="title">{title}</div>
				<div className="content">{content}</div>
				<button onClick={() => setEditMode(true)}></button>
			</div>
		);
	}
}

export default DetailView;
